import Navigation from "@/components/Navigation.vue";
export default {
  name: "Home",
  data() {
    return {
      naviList: ["推荐音乐", "热歌榜", "搜索"],
      routerlist: ["Recommend", "Hotsonglist", "Search"], //根据路由配置中的名字设置
      nvselect: 0
    };
  },
  created() {
    // 当created函数时监测路由信息,防止页面刷新tab高亮错误
    var tmpArr = this.$route.path.split("/");
    if (tmpArr[1] === "Home") {
      this.nvselect = this.routerlist.indexOf(tmpArr[2]); //刷新
    }
  },
  components: { Navigation },
  methods: {
    onItemClick(index) {
      this.$router.push({ path: "/Home/" + this.routerlist[index] });
      //console.log("rou:", this.$router);
      //console.log("rou2:", this.$router.matched);
    }
  },
  // watch函数监测路由的变化
  watch: {
    $route(to, from) {
      //console.log("to:",to);
      // console.log("from:",from);
      const path = to.path;
      var tmpArr = path.split("/");
      if (tmpArr[1] === "Home") {
      }
    }
  }
};
