export default {
  name: "Navigation",
  props: {
    list: Array,
    selectpr:Number
  },
  data () {
    return {
      selectnum:0
    }
  },
  created () {
   this.selectnum=this.selectpr;
    //this.selectnum=index
  },
  methods: {
    onitemselect(index){
      this.selectnum=index
      this.$emit('emitclick',index); //自定义事件click  父组件绑定@emitclick事件
    }
  }
};
