import SoundList from "@/components/SoundList.vue";
import axios from "axios";
export default {
  name: "Search",
  data() {
    return {
      inputtxt: "",
      isShowcont:false,
      searchArr: [], //搜索到的音乐
      hotsearchArr:[],//热搜音乐
      showobjec:{
        isOrder:false,//序号是否显示
        isAlias:false//别名
      }
    };
  },
  components: {
    SoundList
  },
  methods: {
    getHotSearch(){
      //获取热搜
       let url = "http://musicapi.leanapp.cn/search/hot"; //可选参数 : limit : 返回数量 , 默认为 30 
       axios.get(url).then(res => {        
         this.hotsearchArr=res.data.result.hots;
       });
    },
    getSearchInfo() {
      //获取搜索音乐信息 http://api.javaswing.cn/search
      // http://140.143.128.100:3000/  http://musicapi.leanapp.cn
      let url = "http://140.143.128.100:3000/search?keywords="+this.inputtxt; //可选参数 : limit : 返回数量 , 默认为 30 
      axios.get(url).then(res => {
        console.log("搜索音乐:", res);
        this.processData(res);
      });
    },//数据处理
    processData(res){
      var soundArr=[];  
      var songs=res.data.result.songs;
      for(var index in songs){
       var songobject={};
        var song=songs[index];
       // console.log("::::::",res.data,"LLL:",result[index].id)
        songobject.id=song.id;
        songobject.name=song.name;
        songobject.alias=song.alias[0];//别名
        songobject.status=song.status;
       //songobject.artistsname=song.artists[0].name;//艺术家
        var artistsArr=this.getoObjectAttr(song.artists);//艺术家 可能是多个的情况
        var artistsName=this.arrTostring(artistsArr,"/")
        songobject.artistsname=artistsName;
        soundArr.push(songobject);
      }
      this.searchArr=soundArr;
      //console.log("soundArr:",soundArr)
    },
    //获取对象数组中的属性 
    getoObjectAttr(objarr,arrtName){
      var namelist = objarr.map(function (arr) { return arr.name; });      
      return namelist;
    },
    arrTostring(arr,str = "-"){
      var newstr = arr.join(str);
      return newstr;
    },
     stringToarr(stingname, str = "-") {
    var arr = stingname.split(srt);
    return arr;
    },
    itemClick(data) {
      //热门搜索元素点击
      this.inputtxt =data;
      this.getSearchInfo();//搜索
      this.isShowcont=true;//显示搜索内容
    },
    onClose() {
      this.inputtxt = "";
      this.isShowcont=false;//关闭搜索内容
      this.searchArr=[];
    },
    searchGoods(event) { 
      if (event.keyCode == 13) { //如果按的是enter键 13是enter 
          event.preventDefault(); //禁止默认事件（默认是换行） 
          //console.log(event.target.value);
          this.getSearchInfo();//搜索
          this.isShowcont=true;//显示搜索内容
      } 
      },
      onitemClick(data){
      //console.log("data:"+data)
      this.$router.push({ path: "/Song/" + data});
    }
  }
  ,mounted () {
    this.getHotSearch();//
  }
};
