    //数据处理
    processData(res){
      //console.log("res:",res)
       var soundArr=[];  
        var tracks= res.data.playlist.tracks;
       tracks=tracks.length>20?tracks.splice(0,20):tracks;
        //console.log("tracks:",tracks)
        for(var index in tracks){
        var songobject={};
        var song=tracks[index];
        // console.log("::::::",res.data,"LLL:",result[index].id)
          songobject.id=song.id;
          songobject.name=song.name;
          songobject.alias=song.al.name;//别名
          songobject.status=song.status;
         // console.log("LLL:",song.ar)
        //songobject.artistsname=song.ar[0].name;//艺术家
          var artistsArr=this.getoObjectAttr(song.ar);//艺术家 可能是多个的情况
          var artistsName=this.arrTostring(artistsArr,"/")
          songobject.artistsname=artistsName;
          soundArr.push(songobject);
        }
        this.hotSongArr=soundArr;
        //console.log("soundArr:",soundArr)
    },
    //获取对象数组中的属性 
    getoObjectAttr(objarr,arrtName){
      var namelist = objarr.map(function (arr) { return arr.name; });
      return namelist;
    },
    arrTostring(arr,str = "-"){
      var newstr = arr.join(str);
      return newstr;
    },
     stringToarr(stingname, str = "-") {
    var arr = stingname.split(srt);
    return arr;
    },
    onitemClick(data){
     // console.log("data:"+data)
      this.$router.push({ path: "/Song/" + data});
    }
  },
  mounted() {
    this.getHotlist();
  }
};
