import Vue from 'vue'
import App from './App.vue'

import router from './router'
import store from './store'
import '../public/css/reset.css'
import '../public/css/border.css'

Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App),
  mounted() {
    setRem()
  },

}).$mount('#app')
// rem适配
function setRem() {
  var whdef = 750;// 表示750的设计图,使用100PX的默认值
  var bodyWidth = document.body.clientWidth;// 当前窗口的宽度
  //设备宽/设计图 *100 =缩放后字体大小  （html=100px）
  //小于320
  if (bodyWidth < 320) {
    bodyWidth = 320;
  }
  //大于设计图尺寸时
  if (!is_mobile()) {
    if (bodyWidth >= whdef) {
      bodyWidth = whdef;
    }
  }
  var rem = bodyWidth / whdef * 100;// 设备宽/设计图 *100 ,得到该宽度下的相应FONT-SIZE值  
  document.getElementsByTagName('html')[0].style.fontSize = rem + 'px';
}
function is_mobile() {
  var reg = /phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone/i;
  return reg.test(navigator.userAgent.toLowerCase());
}
window.addEventListener('load', setRem);
window.addEventListener('resize', setRem)