import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

import Search from './views/Search.vue'
import Hotsonglist from './views/Hotsonglist.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      children: [{
        name: 'Recommend',
        path: 'Recommend',
        component: () => import('./views/Recommend.vue')
      },
      {
        name: 'Hotsonglist',
        path: 'Hotsonglist',
        component: Hotsonglist
      }, {
        name: 'Search',
        path: 'Search',
        component: Search
      }]
    },
    {
      path: '/Song/:id',
      name: 'Song',
      component: () => import('./views/Song.vue'),
      children: []
    },
    {
      path: '/', 
      redirect: '/Home/Recommend'
    }
  ]
})
